<template>
    <div>
        <ul class="nav">
            <li v-for="(item,index) in navList" :key="index" :class="{navActive:activeChange(index)}" @click="addFocus(index)">
            <router-link :to="item.href">{{item.title}}</router-link>
            </li>
        </ul>
    </div>
</template>
<script>
export default {
    name: 'Vheader',
    data() {
        return {
            navList: [
                {href:'/selfEval',title:'',name:'selfEval'},
                {href:'/projectExcp',title:'',name:'projectExcp'},
                {href:'/workExcp',title:'',name:'workExcp'},
                {href:'/skills',title:'',name:'skills'},
                {href:'/selfInfo',title:'',name:'selfInfo'},
            ],
            numFocus: 4
        }
    },
    methods: {
        addFocus(num) {
            this.numFocus = num
        },
        activeChange(num) {
            if(num == this.numFocus) {
                return true
            }
            return false
        }
    }
}
</script>
<style>
    .nav {
        position: fixed;
        z-index: 100;
        float: right;
        height: 60px;
        width: 100%;
        background-color: #333;
    }
    .nav a {
        color: #fbfbfb;
        display: inline-block;
        width: 100%;
        height: 100%;
    }
    .nav >li {
        float: right;
        padding: 0 10px;
        height: 60px;
        line-height: 60px;
        color: #fff;
    }
    .navActive {
        background-color: #d93;
    }
</style>


