<template>
    <div>
        <div id="container"></div>
    </div>
</template>
<script>
    export default {
        methods: {
            getMap(){
                // AMap.Map.Marker
                let mapObj = new AMap.Map('container', {//'container'id
                    resizeEnable: true, //
                    zoom: 12,//
                    center: [120.129369, 30.284935],//
                });
                let marker = new AMap.Marker({
                        map: mapObj,
                        position: new AMap.LngLat('120.123169', '30.278895'), //
                        title: ''
                })
            }
            
        },
        mounted() {
            this.getMap();
        }
    }
</script>
<style>
    @import url('http://cache.amap.com/lbs/static/main1119.css');
</style>
